import React from 'react';
import { List, ListItem, ListItemText, Paper, makeStyles, Divider } from '@material-ui/core';

const useStyles = makeStyles({
  results: {
    height: '400px',
    overflowY: 'auto',
  },
  listItem: {
    border: '1px solid #e0e0e0',
    marginBottom: '0.5rem',
    borderRadius: '5px',
  },
});

const Results = ({ results, onResultClick }) => {
  const classes = useStyles();

  return (
    <Paper className={classes.results}>
      <List>
        {results.map((result, index) => (
          <div key={index}>
            <ListItem button className={classes.listItem} onClick={() => onResultClick(result)}>
              <ListItemText 
                primary={result.case_title}
                // secondary={result.points[0]}
              />
            </ListItem>
            {index !== results.length - 1 && <Divider />}
          </div>
        ))}
      </List>
    </Paper>
  );
};

export default Results;
