import React from 'react';
import { Card, CardContent, Typography, } from '@material-ui/core';

const Details = ({ result }) => {
  if (!result) return null;

  return (
    <Card>
      <CardContent>
        <Typography variant="h5" gutterBottom>
          {result.case_title}
        </Typography>
        <Typography variant="subtitle1">Key Facts:</Typography>
        <Typography variant="body2" color="textSecondary">
          {result.key_facts}
        </Typography>
        <Typography variant="subtitle1">Issues:</Typography>
        <Typography variant="body2" color="textSecondary">
          {result.issues}
        </Typography>
        <Typography variant="subtitle1">Conclusion:</Typography>
        <Typography variant="body2" color="textSecondary">
          {result.conclusion}
        </Typography>
        <Typography variant="subtitle1">Legal Provision:</Typography>
        <Typography variant="body2" color="textSecondary">
          {result.legal_provision}
        </Typography>
      </CardContent>
    </Card>
  );
};

export default Details;
